<template>
    <div class="music-list">
        <div class="back" @click="back">
            <i class="icon-back"></i>
        </div>
        <h1 class="title" v-html="title"></h1>
        <div class="bg-image" :style="bgStyle" ref="bgImage">
            <div class="play-wrapper">
                <div class="play" v-show="songs.length>0" ref="playBtn" @click="random">
                    <i class="icon-play"></i>
                    <span class="text">随机播放全部</span>
                </div>
            </div>
            <div class="filter" ref="filter"></div>
        </div>
        <div class="bg-layer" ref="layer"></div>
        <scroll :pullup="pullup"
                @scrollToEnd="getSingerMore" @scroll="scroll" :listen-scroll="listenScroll" :probe-type="probeType"
                :data="songs" class="list"
                ref="list">
            <div class="song-list-wrapper">
                <song-list :loadingMore="loadingMore" :rank="rank" @select="selectItem" :songs="songs"></song-list>
            </div>
            <div class="loading-container" v-show="!songs.length">
                <loading></loading>
            </div>
        </scroll>
    </div>
</template>

<script type="text/ecmascript-6">
    import Scroll from 'base/scroll/scroll'
    import SongList from 'base/song-list/song-list'
    import {prefixStyle} from 'common/js/dom'
    import Loading from 'base/loading/loading'
    import {mapActions} from 'vuex'
    import {playlistMixin} from 'common/js/mixin'
    import {ERR_OK} from 'api/config'
    import {getMusic} from 'api/song'
    import {updateUrl} from 'common/js/song'
    // import {deepClone} from 'common/js/util'

    const RESERVED_HEIGHT = 40
    const transform = prefixStyle('transform')
    const backdrop = prefixStyle('backdrop')

    export default {
        mixins: [playlistMixin],
        components: {
            Loading,
            SongList,
            Scroll
        },
        name: 'music-list',
        data() {
            return {
                scrollY: 0,
                pullup: true
            }
        },
        props: {
            bgImage: {
                type: String,
                default: ''
            },
            songs: {
                type: Array,
                default() {
                    return []
                }
            },
            title: {
                type: String,
                default: ''
            },
            rank: {
                type: Boolean,
                default: false
            },
            loadingMore: {
                type: Boolean,
                default: false
            }
        },
        computed: {
            bgStyle() {
                return `background-image:url(${this.bgImage})`
            }
        },
        created() {
            this.probeType = 3
            this.listenScroll = true
        },
        methods: {
            handlePlaylist(playlist) {
                const bottom = playlist.length > 0 ? '60px' : ''
                this.$refs.list.$el.style.bottom = bottom
                this.$refs.list.refresh()
            },
            scroll(pos) {
                this.scrollY = pos.y
            },
            getSingerMore() {
                this.$emit('getSingerMore')
            },
            back() {
                this.$router.back()
            },
            selectItem(item, index) {
                // getMusic(item.mid, 1).then((res) => {
                //     if (res.code === ERR_OK) {
                //         // console.log(res.data.items[0])
                //         updateUrl2(item, res.data.items[0])
                //         // console.log(res)
                //         // this.selectPlay({
                //         //     list: this.songs,
                //         //     index
                //         // })
                //     }
                // })
                getMusic(item.mid, 0).then((res) => {
                    if (res.code === ERR_OK) {
                        // console.log(res.data.items[0])
                        updateUrl(item, res.data.items[0])
                        // console.log(res)
                        this.selectPlay({
                            list: this.songs,
                            index
                        })
                    }
                })
            },
            random() {
                this.randomPlay({
                    list: this.songs
                })
            },
            ...mapActions([
                'selectPlay',
                'randomPlay'
            ])
        },
        watch: {
            scrollY(newY) {
                let translateY = Math.max(this.minTranslateY, newY)
                let zIndex = 0
                let scale = 1
                let blur = 0

                this.$refs.layer.style[transform] = `translate3d(0,${translateY}px,0)`
                // this.$refs.layer.style['webkitTransform'] = `translate3d(0,${translateY}px,0)`
                const percent = Math.abs(newY / this.imageHeight)
                if (newY > 0) {
                    scale = 1 + percent
                    zIndex = 10
                } else {
                    blur = Math.min(20 * percent, 20)
                }
                this.$refs.filter.style[backdrop] = `blur(${blur})`
                // this.$refs.filter.style['webkitBackdrop-filter'] = `blur(${blur})`
                if (newY < this.minTranslateY) {
                    zIndex = 10
                    this.$refs.bgImage.style.paddingTop = 0
                    this.$refs.bgImage.style.height = `${RESERVED_HEIGHT}PX`
                    this.$refs.playBtn.style.display = 'none'
                } else {
                    this.$refs.bgImage.style.paddingTop = '70%'
                    this.$refs.bgImage.style.height = 0
                    this.$refs.playBtn.style.display = ''
                }
                this.$refs.bgImage.style.zIndex = zIndex
                this.$refs.bgImage.style[transform] = `scale(${scale})`
                // this.$refs.bgImage.style['webkitTransform'] = `scale(${scale})`
            }
        },
        mounted() {
            this.imageHeight = this.$refs.bgImage.clientHeight
            this.minTranslateY = -this.imageHeight + RESERVED_HEIGHT
            this.$refs.list.$el.style.top = `${this.$refs.bgImage.clientHeight}px`
            // console.log(this.$refs.bgImage.clientHeight)
        }
    }
</script>

<style lang="stylus" rel="stylesheet/stylus">
    @import "~common/stylus/variable"
    @import "~common/stylus/mixin"
    .music-list
        position fixed
        z-index 100
        top 0
        left 0
        right 0
        bottom 0
        background $color-background
        .back
            position absolute
            top 0px
            left 10px
            z-index: 50
            .icon-back
                display: block
                padding: 10px
                font-size: $font-size-large-x
                color: $color-theme
        .title
            position absolute
            top 15px
            left 50%
            width 240px
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            transform translate3d(-50%, 0, 0)
            text-align center
            z-index: 40
        .bg-image
            position: relative
            width: 100%
            height: 0
            padding-top: 70%
            transform-origin: top
            background-size: cover
            .play-wrapper
                position: absolute
                bottom: 20px
                z-index: 50
                width: 100%
                .play
                    box-sizing: border-box
                    width: 135px
                    padding: 7px 0
                    margin: 0 auto
                    text-align: center
                    border: 1px solid $color-theme
                    color: $color-theme
                    border-radius: 100px
                    font-size: 0
                    .icon-play
                        display: inline-block
                        vertical-align: middle
                        margin-right: 6px
                        font-size: $font-size-medium-x
                    .text
                        display: inline-block
                        vertical-align: middle
                        font-size: $font-size-small
            .filter
                position: absolute
                top: 0
                left: 0
                width: 100%
                height: 100%
                background: rgba(7, 17, 27, 0.4)
        .bg-layer
            position: relative
            height: 100%
            background: $color-background
        .list
            position: fixed
            top: 0
            left 0
            bottom: 0
            width: 100%
            background: $color-background
            .song-list-wrapper
                padding: 20px 30px
            .loading-container
                position: absolute
                width: 100%
                top: 50%
                transform: translateY(-50%)
</style>



